@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  background: #fff;
  color: #222;
  min-height: 100vh;
}

#root {
  min-height: 100vh;
}

#radar-description {
  margin: auto;
  text-align: left;
  display: inline-block;
  font-size: 1rem;
  color: #222;
}

#adopt {
  display: inline;
  color: #5ba300;
  font-weight: bold;
}

#trial {
  display: inline;
  color: #009eb0;
  font-weight: bold;
}

#assess {
  display: inline;
  color: #c7ba00;
  font-weight: bold;
}

#hold {
  display: inline;
  color: #e09b96;
  font-weight: bold;
}

#logo {
  margin: 2vw;
  width: 100vw;
  max-width: 300px;
}

#footer {
  margin-bottom: 3vw;
  font-size: 12px;
  color: #888;
}

/* --- Solidgate.com inspired styles --- */

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Inter", Arial, Helvetica, sans-serif;
  color: #181a20;
  font-weight: 700;
  margin-top: 0;
}

.card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(24, 26, 32, 0.08);
  padding: 2rem;
  margin-bottom: 2rem;
  text-align: left;
}

.button,
button {
  font-family: "Inter", Arial, Helvetica, sans-serif;
  background: #5ba300;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 0.75rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(91, 163, 0, 0.08);
}
.button:hover,
button:hover {
  background: #489000;
}

.section-title {
  color: #181a20;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

::-webkit-scrollbar {
  width: 8px;
  background: #eee;
}
::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 8px;
}

/* Media blocks hover effect */
.media-block {
  background: #f7f8fa;
  border-radius: 12px;
  padding: 20px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.media-block:hover {
  transform: translateY(-2px);
  background: #e8f5e8;
}
